.main-login-component {
    height: 100%;
    min-height: 100vh;
}

.intro-image-div {
    border-radius: 0px 20px 20px 0px;
    background: var(--new, linear-gradient(130deg, #492FB2 -1.74%, #ADADAD 108.02%));
    background-image: url('../../../assets/images/new-banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 40%;
}

.login-form-div {
    width: 60%;
    padding: 20px;

    h1 {
        font-family: Urbanist;
        font-size: 40px;
        font-style: normal;
        font-weight: 500;
        line-height: 130%;
        /* 52px */
        background: var(--2, linear-gradient(90deg, #222 0%, #424242 100%));
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

.inneradjustment,
.second-step {
    width: 100%;
    max-width: 500px;
}

form {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-top: 48px;

    label {
        color: var(--Text-Dark, #212121);
        font-family: Urbanist;
        font-size: var(--Font-Size-2, 14px);
        font-style: normal;
        font-weight: 500;
        line-height: var(--Line-Heights-10, 20px);
        /* 142.857% */
        letter-spacing: var(--Letter-Spacing-2, 0.1px);
        margin-bottom: 4px;
    }

    input[type="email"],
    input[type="password"],
    input[type="text"] {
        padding: 12px;
        border-radius: 8px;
        border: 1px solid #ccc;
        font-size: 14px;
        width: 100%;
        box-sizing: border-box;
    }

    .password-input {
        position: relative;

        input {
            width: 100%;
            padding-right: 40px;
        }

        .eye-button {
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);
            background: transparent;
            border: none;
            cursor: pointer;
            padding: 0;

            img {
                width: 20px;
            }
        }
    }

    .forgot {
        text-align: right;
        margin-top: -8px;

        a {
            color: #3b27b9;
            font-size: 14px;
            text-decoration: none;
        }
    }

    .login-btn {
        border-radius: 8px;
        border: 1px solid;
        background: #222;
        color: var(--Text-Light, #FFF);
        text-align: center;
        font-family: Urbanist;
        font-size: var(--Font-Size-3, 16px);
        font-style: normal;
        font-weight: 500;
        line-height: var(--Line-Heights-9, 24px);
        /* 150% */
        letter-spacing: var(--Letter-Spacing-1, 0.15px);
        padding: 12px;

    }

    .error-message {
        color: red;
    }

    .login-btn:disabled {
        padding: 12px;
        border: none;
        border-radius: 8px;
        background-color: #a0a0a0 !important;
        color: #fff;
        font-size: 16px;
        cursor: not-allowed;
    }

    .signup-text {
        font-size: 14px;
        text-align: center;

        a {
            color: #3b27b9;
            text-decoration: none;
            font-weight: 500;
        }
    }

    .warning-box {
        display: flex;
        align-items: center;
        background-color: #fff4db;
        border-radius: 8px;
        padding: 12px;
        gap: 8px;
        color: #333;

        p {
            color: #A16800;
            font-family: Urbanist;
            font-size: 12px;
            font-style: normal;
            font-weight: 500;
            line-height: 18px;
            /* 150% */
            margin: 0;
        }
    }
}

.second-step {
    h1 {
        font-family: Urbanist;
        font-size: 40px;
        font-style: normal;
        font-weight: 500;
        line-height: 130%;
        /* 52px */
        background: var(--2, linear-gradient(90deg, #222 0%, #424242 100%));
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    h4 {
        color: var(--Neutral-700, #7E838B);
        font-family: Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.otp-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.otp-inputs {
    display: flex;
    gap: 10px;
    margin: 20px 0;
}

.otp-inputs input {
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
    text-align: center;
    border: 2px solid #ccc;
    border-radius: 8px;
    outline: none;
    transition: border-color 0.2s;
}

.otp-inputs input:focus {
    border-color: black;
}

.password-strength {
    margin-right: 8px;
    font-weight: 600;
    font-size: 0.85rem;
    text-transform: uppercase;

    &.poor {
        color: red;
    }

    &.good {
        color: #b8860b;
    }

    // dark yellow/orange
    &.best {
        color: green;
    }
}



/* ====== Base (your existing code is good) ====== */

/* ====== Tablet (≤ 992px) ====== */
@media (max-width: 992px) {
  .intro-image-div {
    width: 50%;
  }
  .login-form-div {
    width: 50%;
    padding: 40px 20px;
  }
  .login-form-div h1 {
    font-size: 32px;
  }
}

/* ====== Mobile (≤ 768px) ====== */
@media (max-width: 768px) {
  .main-login-component {
    flex-direction: column;
    justify-content: flex-start;
  }

  .intro-image-div {
    width: 100%;
    height: 520px;
    border-radius: 0 0 20px 20px;
    background-position: center;
  }

  .login-form-div {
    width: 100%;
    padding: 30px 20px;
  }

  .login-form-div h1 {
    font-size: 28px;
    line-height: 120%;
    text-align: center;
  }

  form {
    margin-top: 32px;
    gap: 12px;
  }

  form label {
    font-size: 13px;
  }

  form input[type="email"],
  form input[type="password"],
  form input[type="text"] {
    padding: 10px;
    font-size: 14px;
  }

  .login-btn {
    font-size: 15px;
    padding: 10px;
  }

  .signup-text {
    font-size: 13px;
  }

  .warning-box {
    padding: 10px;
    font-size: 12px;
    align-items: flex-start;
  }
  .warning-box svg {
    flex-shrink: 0;
    margin-top: 2px;
  }
}

/* ====== Small Mobile (≤ 480px) ====== */
@media (max-width: 480px) {
  .intro-image-div {
    height: 520px;
    border-radius: 0;
  }

  .login-form-div h1 {
    font-size: 24px;
  }

  .otp-inputs {
    gap: 6px;
  }
  .otp-inputs input {
    width: 42px;
    height: 42px;
    font-size: 1.2rem;
  }

  .login-btn {
    font-size: 14px;
    padding: 8px;
  }
}
